.userDetail{
    flex:4;
}
.userTitleContainer{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.userAddButton{
    border:none;
    width:80px;
    padding:5px;
    background-color: teal;
    border-radius: 5px;
    cursor: pointer;
    color:white;
    font-size: 16px;
}

.userContainerTable{
    display: flex;

}
.userShow{
    flex:1;
    padding: 20px;
    box-shadow: 13px 5px 30px 0px rgba(0,0,0,0.75);
    -webkit-box-shadow: 13px 5px 30px 0px rgba(0,0,0,0.75);
    -moz-box-shadow: 13px 5px 30px 0px rgba(0,0,0,0.75);

}
.userUpdate{
    flex:2;
    padding: 20px;
    box-shadow: 13px 5px 30px 0px rgba(0,0,0,0.75);
    -webkit-box-shadow: 13px 5px 30px 0px rgba(0,0,0,0.75);
    -moz-box-shadow: 13px 5px 30px 0px rgba(0,0,0,0.75);
    margin-left: 20px;
}

.userShowTop{
    display: flex;
    align-items: center;
}
.userShowImg{
    width:40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}
.userShowTopTitle{
    display: flex;
    flex-direction: column;
    margin-left: 20px;
}
.userShowUsername{
    font-weight: 900;
}
.userShowUserTitle{
    font-weight: 300;
}

.userShowBottom{
    margin-top: 20px;
}
.userShowTitle{
    font-size: 14px;
    font-weight: 600;
    color:lightgray;

}
.userShowInfo{
    display: flex;
    align-items: center;
    margin:20px 0px;
    color:#444;
}

.userShowIcon{
    font-size: 16px !important;
}

.userShowInfoTitle{
    margin-left: 10px;
}
.userUpdateProfile{
    font-size: 24px;
}

.userUpdateForm{
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
}



.userUpdatetem{
    display: flex;
    flex-direction: column;
    margin-top: 10px;
}
.userUpdatetem >label{
    margin-bottom: 5px;
    font-size: 14px;
}


.userUpdateInput{
    border: none;
    width:250px;
    height: 30px;
    border-bottom: 1px solid gray;
}

.userUpdateRight{
   display: flex;
   flex-direction: column;
   justify-content:space-between;
}

.userUpdateUpload{
    display: flex;
    align-items: center;
}
.userUpdateIcon{
    cursor: pointer;
}

.userUpdateImg{
    width:100px;
    height: 100px;
    border-radius: 10px;
    object-fit: cover;
    margin-right: 20px;
}
.userUpdateButton{
    border:none;
    border-radius: 5px;
    padding:5px;
    cursor:pointer;
    background-color: darkblue;
    color:white;
    font-weight: 900;
}
